<template>
  <div id="app">
    <header>
      <!--  ,  -->
      <div class="top-header">
        <div class="lefttop">   </div>
        <div class="auth">
          <a href="#" class="login" @click="goToLogin"></a>
          <a href="#" class="signup" @click="goToJoin"></a>
        </div>
      </div>

      <!--     -->
      <div class="bottom-header">
        <div class="logo">
          <!-- DADOK   MainPage  -->
          <img src="@/assets/Group (1).png" alt="Logo" />
          <h1 @click="goToMain" style="cursor: pointer;">DADOK</h1>
        </div>
        <nav>
          <ul>
            <li><a href="#"></a></li>
            <li><a href="#" class="monthbook" @click="goToMonthBook"></a></li>
            <li><a href="#"></a></li>
          </ul>
        </nav>
        <div class="profile">
          <a href="#"><img @click="goToMyPage" src="@/assets/profileicon.png" alt="Profile" /></a>
        </div>
      </div>
    </header>

    <main>
      <section class="signup-form">
        <h2></h2>
        <form @submit.prevent="submitForm">
          <div class="form-group">    
            <input type="text" id="id" placeholder="" v-model="userId" /> <!-- userId  -->
          </div>
          <div class="form-group">
            <input type="password" id="password" placeholder="" v-model="password" />
          </div>
          <div v-if="error" class="error-message">{{ error }}</div> <!--    -->
          <div class="form-login">
            <button type="submit" class="submit-btn"></button>
          </div>
          <div class="signup-prompt">
            <span> ?</span>
          </div>
          <div class="form-signup">
            <button @click="goToJoin" class="signup-btn"></button>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data() {
    return {
      userId: '',
      password: '',
      error: ''
    };
  },
  methods: {
    submitForm() {
      const loginData = { username: this.userId, password: this.password };

      //   
      this.$axios.post(`http://25.6.251.212:3000/login`, loginData, { withCredentials: true })
        .then(response => {
          console.log('  :', response);

          if (response && response.data && response.data.success) {
            this.$router.push('/mypage'); //    
          } else {
            throw new Error(' ');
          }
        })
        .catch(err => {
          console.error('  :', err);
          
          if (err.response && err.response.data && err.response.data.message) {
            this.error = ' : ' + err.response.data.message;
          } else {
            this.error = '   .';
          }
        });
    },

    goToMain() {
      this.$router.push({ name: 'MainPage' });
    },

    goToJoin() {
      this.$router.push({ name: 'JoinPage' });
    },

    goToLogin() {
      this.$router.push({ name: 'LoginPage' });
    },

    goToMonthBook() {
      this.$router.push({ name: 'BookPage10' });
    },

    goToMyPage() {
      this.$router.push({ name: "myPage" });
    },

    goToBoard() {
      this.$router.push({ name: "PostListView" });
    }
  }
};
</script>
    
    <style scoped>
    
    
