<template>
  <div id="app">
    <header>
      <!--  ,  -->
      <div class="top-header">
        <div class="lefttop">   </div>
        <div class="auth">
          <div v-if="isAuthenticated">
            <a href="#" class="logout" @click="logout"></a>
          </div>
          <div v-else>
            <a href="#" class="login" @click="goToLogin"></a>
            <a href="#" class="signup" @click="goToJoin"></a>
          </div>
        </div>
      </div>

      <!--     -->
      <div class="bottom-header">
        <div class="logo">
          <!-- DADOK   MainPage  -->
          <img src="@/assets/Group (1).png" alt="Logo" />
          <h1 @click="goToMain" style="cursor: pointer;">DADOK</h1>
        </div>
        <nav>
          <ul>
            <li><a href="#" class="Board" @click="goToBoard"></a></li>
            <li><a href="#" class="monthbook" @click="goToMonthBook"></a></li>
            <li><a href="#"></a></li>
          </ul>
        </nav>
        <div class="profile">
          <a href="#"><img @click="goToMyPage" src="@/assets/profileicon.png" alt="Profile" /></a>
        </div>
      </div>
    </header>

    <!-- ...   ... -->

  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      profileImage: "@/assets/default-profile.png",
      user: {},
      username: "",
      userId: null,
      isNicknameModalVisible: false,
      newNickname: "",
      isDeleteModalVisible: false,
    };
  },
  created() {
    //      
    axios
      .get(`http://172.16.111.42:8080/mypage`, { withCredentials: true })
      .then((response) => {
        this.user = response.data;
        console.log('  :', this.user);
      })
      .catch((error) => {
        console.error('     :', error);
        this.$router.push('/login'); //     
      });
  },
  computed: {
    isAuthenticated() {
      return this.user && Object.keys(this.user).length > 0;
    }
  },
  methods: {
    logout() {
      //   
      axios
        .post('http://25.6.251.212:3000/logout', {}, { withCredentials: true })
        .then(() => {
          this.user = {}; //   
          this.$router.push('/'); //   
        })
        .catch((error) => {
          console.error('   :', error);
        });
    },
    goToMain() {
      this.$router.push({ name: "MainPage" });
    },
    goToJoin() {
      this.$router.push({ name: "JoinPage" });
    },
    goToLogin() {
      this.$router.push({ name: "LoginPage" });
    },
    goToMonthBook() {
      this.$router.push({ name: "BookPage10" });
    },
    goToMyPage() {
      this.$router.push({ name: "myPage" });
    },
    goToBoard() {
      this.$router.push({ name: "PostListView" });
    },
    //  
  }
};
</script>


