<!-- src/views/PostList.vue -->
<template>
  <header>
      <!--  ,  -->
      <div class="top-header">
        <div class="lefttop">   </div>
        <div class="auth">
          <div v-if="isAuthenticated">
            <a href="#" class="logout" @click="logout"></a>
          </div>
          <div v-else>
            <a href="#" class="login" @click="goToLogin"></a>
            <a href="#" class="signup" @click="goToJoin"></a>
          </div>
        </div>
      </div>

      <!--     -->
      <div class="bottom-header">
        <div class="logo">
          <!-- DADOK   MainPage  -->
          <img src="@/assets/Group (1).png" alt="Logo" />
          <h1 @click="goToMain" style="cursor: pointer;">DADOK</h1>
        </div>
        <nav>
          <ul>
            <li><a href="#" class="Board" @click="goToBoard"></a></li>
            <li><a href="#" class="monthbook" @click="goToMonthBook"></a></li>
            <li><a href="#"></a></li>
          </ul>
        </nav>
        <div class="profile">
          <a href="#"><img @click="goToMyPage" src="@/assets/profileicon.png" alt="Profile" /></a>
        </div>
      </div>
    </header>
    <main>
      <section class="boardpage-form">
        <div class="form-board">
          <button class="btn" @click="goToBoard"></button>
          <button class="btn2" @click="goToCreate"></button>
        </div>
        <hr class="custom-line">

        <!--   -->
        <div v-for="post in posts" :key="post.id" @click="goToDetail(post.id)" class="post-item">
          <img :src="post.image" alt="" class="post-image" />
      <h3>{{ post.title }}</h3>
      <!-- <p>{{ post.content.substring(0, 50) }}...</p> -->
    </div>
      </section>
    </main>

</template>

<script>
export default {
  computed: {
    posts() {
      return this.$store.state.posts;
    }
  },
  methods: {
    goToDetail(id) {
      this.$router.push({ name: 'PostDetailView', params: { id } });
    },
    goToCreate() {
      this.$router.push({ name: 'PostCreateView' });
    },
    logout() {
      localStorage.removeItem('token'); //   JWT 
      this.$store.commit('logout'); // Vuex  
      this.$router.push('/'); //   
    },
    goToMain() {
      this.$router.push({ name: 'MainPage' });
    },
    goToJoin() {
      this.$router.push({ name: 'JoinPage' });
    },
    goToLogin() {
      this.$router.push({ name: 'LoginPage' });
    },
    goToBoard() {
      // BoardPage 
      this.$router.push({ name: "PostListView" });
    },
    goToMonthBook() {
      this.$router.push({ name: 'BookPage10' });
    },
    goToMyPage() {
      this.$router.push({ name: "myPage" });
    },


  }
};
</script>

<style scoped>
/* Header Styles */
header {
  background-color: white;
  border-bottom: 1px solid #eaeaea;
}

/*  /  */
.top-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eaeaea;
}

.lefttop {
  font-size: 12px;
  color: #a1a1a1;
  margin-left: 65px;
}

.auth a {
  margin-left: 30px;
  text-decoration: none;
  color: black;
  font-size: 14px;
}

.auth .signup {
  margin-right: 60px;
  background-color: #f4c4b7;
  padding: 5px 15px;
  border-radius: 5px;
  color: white;
}
.auth .logout {
    margin-right: 60px;
    background-color: #f4c4b7;
    padding: 5px 15px;
    border-radius: 5px;
    color: white;
  }
/*     */
.bottom-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
  color: #f4c4b7;
  height: 70px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  height: 25px;
  margin-right: 7px;
}

nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin: 0 50px;
}

nav ul li a {
  text-decoration: none;
  color: black;
  font-size: 14px;
}

.profile {
  margin-right: 25px;
}

.profile img {
  height: 35px;
  border-radius: 50%;
}
/* Main Section Styles */
.boardpage-form {
  margin-top: 10px;
  text-align: center;
  padding: 50px 250px; /*    (20px) */
}

.form-board {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; /*      */
}

.btn, .btn2 {
  width: 100px; /*     */
  height: 45px;
  padding: 10px;
  border: none;
  border-radius: 30px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  margin: 0 100px; /*    */
}

.btn {
  background-color: #f4c4b7;
}

.btn2 {
  background-color: #C0C0C0;
}

.btn:hover {
  background-color: #e3a999;
}

.btn2:hover {
  background-color: #848484;
}

.custom-line {
  border: 0;
  height: 1px; /*   */
  background-color: #000000; /*   */
  margin: 20px 0; /*    */
}

/*   */
@media (max-width: 768px) {
  .boardpage-form {
    padding: 50px 10px; /*   */
  }

  .btn {
    width: 120px; /*    */
  }

  .btn2 {
    width: 120px; /*    */
  }

}
.post-item {
  cursor: pointer;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}
.post-image {
  width: 10%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 00px;
}

</style>
