<template>
    <div id="app">
      <header>
        <!--  ,  -->
        <div class="top-header">
          <div class="lefttop">   </div>
          <div class="auth">
            <a href="#" class="login" @click="goToLogin"></a>
            <a href="#" class="signup"></a>
          </div>
        </div>
  
        <!--     -->
        <div class="bottom-header">
          <div class="logo">
            <!-- DADOK   MainPage  -->
            <img src="@/assets/Group (1).png" alt="Logo" />
            <h1 @click="goToMain" style="cursor: pointer;">DADOK</h1>
          </div>
          <nav>
            <ul>
              <li><a href="#"></a></li>
              <li><a href="#" class="monthbook" @click="goToMonthBook"></a></li>
              <li><a href="#"></a></li>
            </ul>
          </nav>
          <div class="profile">
            <a href="#"><img @click="goToMyPage" src="@/assets/profileicon.png" alt="Profile" /></a>
          </div>
        </div>
      </header>
  
      <main>
        <section class="signup-form">
    <h2></h2>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="username"> *</label>
        <input type="text" id="username" placeholder="" v-model="username" />
      </div>
      <div class="form-group">
        <label for="userid"> *</label>
        <input type="text" id="userid" placeholder="" v-model="userid" />
      </div>
      <div class="form-group">
        <label for="password"> *</label>
        <input type="password" id="password" placeholder="" v-model="password" />
      </div>
      <div class="form-group">
        <label for="confirmPassword">  *</label>
        <input type="password" id="confirmPassword" placeholder=" " v-model="confirmPassword" />
      </div>
      <button type="submit" class="submit-btn"></button>
    </form>
    <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
    <p v-if="successMessage" class="success">{{ successMessage }}</p>
  </section>
  
      </main>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  export default {
    name: 'JoinPage',
  
    data() {
      return {
        username: '',
        userid: '',
        password: '',
        confirmPassword: '',
        errorMessage: '',
        successMessage: ''
        
      };
    },
    methods: {
    //   
    validatePassword() {
      if (this.password !== this.confirmPassword) {
        this.errorMessage = "  .";
        return false;
      }
      return true;
    },
  
    //     
    validateForm() {
      if (!this.username || !this.userid || !this.password || !this.confirmPassword) {
        this.errorMessage = "  .";
        return false;
      }
  
      //  
      if (!this.validatePassword()) {
        return false;
      }
      return true;
    },
  
    //   
    submitForm() {
      //   
      if (!this.validateForm()) {
        return;
      }
  
      const signupData = {
        username: this.username,
        userid: this.userid,
        password: this.password
      };
  
      axios.post(`http://25.6.251.212:3000/register`, signupData, { timeout: 5000 })
        .then(() => {
          this.successMessage = "  .";
          this.errorMessage = '';
          this.username = '';
          this.userid = '';
          this.password = '';
          this.confirmPassword = '';  //    
  
          //    MainPage 
          this.goToMain();
        })
        .catch(error => {
          if (error.code === 'ECONNABORTED') {
            console.error('  :', error.message);
            this.errorMessage = '    .  .';
          } else if (error.response) {
            console.error('  :', error.response.data);
            this.errorMessage = ` : ${error.response.data.message}`;
          } else if (error.request) {
            console.error(' :', error.request);
            this.errorMessage = '  .';
          } else {
            console.error('  :', error.message);
            this.errorMessage = '    .';
          }
        });
    },
  
    goToMain() {
      this.$router.push({ name: 'MainPage' });
    },
    goToLogin() {
      this.$router.push({ name: 'LoginPage' });
    },
    goToMonthBook() {
      this.$router.push({ name: 'BookPage10' });
    },
    goToMyPage() {
      this.$router.push({ name: "myPage" });
    },
    goToBoard() {
        // BoardPage 
        this.$router.push({ name: "BoardPage" });
      },
  }
  
  };
  </script>
  
  <style scoped>
  /* Global Styles */
  body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f5f5f5;
  }
  
  /* Header Styles */
  header {
    background-color: white;
    border-bottom: 1px solid #eaeaea;
  }
  
  /*  /  */
  .top-header {
    display: flex;
    justify-content: space-between;  /*      */
    align-items: center;  /*    */
    padding: 0% 30px;
    padding-bottom: 10px; /*    */
    border-bottom: 1px solid #eaeaea;
  }
  /*     */
  .lefttop {
    font-size: 12px;
    color:#a1a1a1;
    margin-left: 65px;
  }
  .auth a {
    margin-left: 30px;
    text-decoration: none;
    color: black;
    font-size: 14px;
  }
  .auth .signup {
    margin-right: 60px;
    background-color: #f4c4b7;
    padding: 5px 15px;
    border-radius: 5px;
    color: white;
  }
  
  /*     */
  .bottom-header {
    display: flex;
    justify-content: space-between; /*     */
    align-items: center;
    padding: 0px 80px;
    color: #f4c4b7;
    height: 70px;
  }
  
  .logo {
    display: flex;
    align-items: center;
    margin-right: -70px;
  }
  
  .logo img {
    height: 25px;
    margin-right: 7px;
  }
  
  nav ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  nav ul li {
    margin: 0 50px;
  }
  
  nav ul li a {
    text-decoration: none;
    color: black;
    font-size: 14px;
  }
  
  .profile {
    margin-right: 25px;
  }
  
  .profile img {
    height: 35px;
    border-radius: 50%;
  }
  
  /* Main Signup Form Styles */
  .signup-form {
    text-align: center;
    padding: 50px 0;
  }
  
  .signup-form h2 {
    font-size: 24px;
    margin-bottom: 30px;
  }
  
  form {
    display: inline-block;
    text-align: left;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
  }
  
  .form-group input {
    width: 300px;
    padding: 10px;
    border: 1px solid #eaeaea;
    border-radius: 5px;
  }
  .form-group input::placeholder {
    color: #cacaca; 
  }
  
  .submit-btn {
    margin-top: 20px;
    width: 100%;
    padding: 10px;
    background-color: #f4c4b7;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }
  
  .submit-btn:hover {
    background-color: #e3a999;
  }
  .error {
    color: red;
    margin-top: 10px;
  }
  
  .success {
    color: green;
    margin-top: 10px;
  }
  </style>
  