import { createRouter, createWebHistory } from 'vue-router';
import MainPage from '@/components/MainPage.vue'; //   
import JoinPage from '@/components/JoinPage.vue'; //   
import LoginPage from '@/components/LoginPage.vue';//   
import BoardPage from '@/components/BoardPage.vue';//   
import myPage from '@/components/myPage.vue'; // 
//import MonthDecember from '@/components/MonthBook/MonthDecember.vue'; // 12 
//import MonthNovember from '@/components/MonthBook/MonthNovember.vue'; // 11 
import MonthBookpage from '@/components/MonthBookPage.vue'; //  10   ( October )
import MonthSeptember from '@/components/MonthBook/MonthSeptember.vue'; // 9 
import MonthAugust from '@/components/MonthBook/MonthAugust.vue'; // 8 
import MonthJuly from '@/components/MonthBook/MonthJuly.vue'; // 7 
import MonthJune from '@/components/MonthBook/MonthJune.vue'; // 6 
import MonthMay from '@/components/MonthBook/MonthMay.vue'; // 5 
import MonthApril from '@/components/MonthBook/MonthApril.vue'; // 4 
import MonthMarch from '@/components/MonthBook/MonthMarch.vue'; // 3 
import MonthFebruary from '@/components/MonthBook/MonthFebruary.vue'; // 2 
import MonthJanuary from '@/components/MonthBook/MonthJanuary.vue'; // 1 
import PostListView from '@/components/posts/PostListView.vue'; // 
import PostCreateView from '@/components/posts/PostCreateView.vue'; // 
import PostDetailView from '@/components/posts/PostDetailView.vue'; // 
import PostEditView from '@/components/posts/PostEditView.vue'; // 



const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage, //  
  },
  {
    path: '/JoinPage',
    name: 'JoinPage',
    component: JoinPage, //  
  },
  {
    path: '/LoginPage',
    name: 'LoginPage',
    component: LoginPage, //  
  },
  {
    path: '/BoardPage',
    name: 'BoardPage',
    component: BoardPage, //  
    // meta: { requiresAuth: true }
  },
  {
    path: '/myPage',
    name: 'myPage',
    component: myPage, // 
    meta: { requiresAuth: true } // 
  },
  {
    path: '/post',
    name: 'PostListView',
    component: PostListView,
  },
  {
    path: '/posts/create',
    name: 'PostCreateView',
    component: PostCreateView,
  },
  {
    path: '/posts/:id',
    name: 'PostDetailView',
    component: PostDetailView,
    props: true,
  },
  {
    path: '/posts/:id/edit',
    name: 'PostEditView',
    component: PostEditView,
  },


  // 1~12 
  // {
  //   path: '/MonthDecember',
  //   name: 'MonthDecember',
  //   component: MonthDecember, //  12 
  // },
  // {
  //   path: '/MonthNovember',
  //   name: 'MonthNovember',
  //   component: MonthNovember, //  11 
  // },
  {
    path: '/MonthBookPage',
    name: 'BookPage10',
    component: MonthBookpage, //  10 
  },
  {
    path: '/MonthSeptember',
    name: 'BookPage9',
    component: MonthSeptember, //  9 
  },
  {
  path: '/MonthAugust',
  name: 'BookPage8',
  component: MonthAugust, //  8 
  },
  {
    path: '/MonthJuly',
    name: 'BookPage7',
    component: MonthJuly, //  7 
  },
  {
    path: '/MonthJune',
    name: 'BookPage6',
    component: MonthJune, //  6 
  },
  {
    path: '/MonthMay',
    name: 'BookPage5',
    component: MonthMay, //  5 
  },
  {
    path: '/MonthApril',
    name: 'BookPage4',
    component: MonthApril, //  4 
  },
  {
    path: '/MonthMarch',
    name: 'BookPage3',
    component: MonthMarch, //  3 
  },
  {
    path: '/MonthFebruary',
    name: 'BookPage2',
    component: MonthFebruary, //  2 
  },
  {
    path: '/MonthJanuary',
    name: 'BookPage1',
    component: MonthJanuary, //  1 
  },

  

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const isAuthenticated = !!localStorage.getItem('token');
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!isAuthenticated) {
      alert('   .');
      next('/LoginPage'); //      
    } else {
      next(); //     
    }
  } else {
    next(); //      
  }
});


export default router;
