import { createStore } from 'vuex';
import axios from 'axios';

const store = createStore({
  state: {
    isAuthenticated: false, //   
    user: null, //    ( )
    posts: JSON.parse(localStorage.getItem('posts')) || []
  },
  mutations: {
    setUser(state, user) {
      state.isAuthenticated = true;
      state.user = user; //     
    },
    clearUser(state) {
      state.isAuthenticated = false;
      state.user = null; //     
    },
    addPost(state, post) {
      state.posts.push(post);
      localStorage.setItem('posts', JSON.stringify(state.posts));
    }
  },
  actions: {
    async checkSession({ commit }) {
      try {
        const response = await axios.get('http://25.6.251.212:3000/check-session', { withCredentials: true });
        if (response.data.isAuthenticated) {
          commit('setUser', response.data.user); //      
        }
      } catch (error) {
        console.error('    :', error);
      }
    },
    async login({ commit }, loginData) {
      try {
        const response = await axios.post('http://25.6.251.212:3000/login', loginData, { withCredentials: true });
        commit('setUser', response.data.user); //      
      } catch (error) {
        throw new Error(' : ' + error.response.data.message);
      }
    },
    async logout({ commit }) {
      try {
        await axios.post('http://25.6.251.212:3000/logout', {}, { withCredentials: true });
        commit('clearUser'); //      
      } catch (error) {
        console.error('   :', error);
      }
    },
    //  
    addPost({ commit }, post) {
      commit('addPost', post); //   
    }
  },
  getters: {
    posts: state => state.posts,
    getPostById: (state) => (id) => state.posts.find(post => post.id === id)
  }
});

export default store;
